    &__row {
      @include center;
      @include r(767) {
        flex-direction: column; } }
    p {
      font-size: 18px;
      font-weight: normal;
      line-height: 1.39;
      color: #131c2e;
      @include r(767) {
        text-align: center; } }
    &__block {
      &-primary {
        max-width: 395px;
