  &__plans {
    padding: 90px 0 180px;
    @include r(767) {
      padding: 50px 0; }
    &__row {
      display: flex;
      align-items: flex-end;
      @include r(991) {
        flex-direction: column;
        align-items: center; } }
    &__block {
      background: #f4f4f4;
      margin: 0 20px;
      padding: 50px 0;
      width: 100%;
      padding-top: 0;
      border-radius: 15px;
      min-height: 550px;
      position: relative;
      @include r(991) {
        padding: 30px 10px;
        margin: 0;
