        top: -40px;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px; } }
    &__text {
      padding: 0 50px;
      @include r(1200) {
        padding: 0 10px; }
      h3 {
        font-size: 50px;
        font-weight: bold;
        line-height: 1.1;
        letter-spacing: -1px;
        text-align: center;
        color: #131c2e;
        margin-bottom: 30px;
        padding-top: 50px;
        @include r(767) {
          padding-top: 0px;
          margin-bottom: 10px; } }
      strong {
        font-size: 14px;
        font-weight: bold;
        text-align: center;
        color: #131c2e;
        display: block;
        margin-bottom: 35px;
        @include r(767) {
          margin-bottom: 10px; }
        b {
          font-size: 30px; } }
      a.button {
        width: 180px;
        height: 50px;
        border-radius: 25px;
        background-color: #131c2e;
        border: 1px solid #131c2e;
        font-size: 18px;
        font-weight: bold;
        line-height: 1.72;
        color: #ffffff;
        @include relative-x;
        @include center;
        margin-bottom: 50px;
        @include r(767) {
          margin-bottom: 40px; }
        &:hover {
          background: none;
          color: #131c2e; } }
      a.all {
        font-size: 18px;
        font-weight: bold;
        text-align: center;
        color: #131c2e;
        text-decoration: underline;
        margin-bottom: 30px;
        display: block; }
      li {
        font-size: 18px;
        font-weight: normal;
        line-height: 1.94;
        text-align: center;
        color: #131c2e; } } } }
