.landing-home {
  .baner-landing {
    font-size: 14px;
    font-weight: bold;
    text-align: center;
    color: #ffffff;
    padding: 15px;
    display: block;
    background: #000;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 10;
    a {
      color: #ff0057;
      &:hover {
        text-decoration: underline; }
      i {
        margin-left: 6px;
        font-size: 9px;
        font-weight: 900; } } }
  .header-landing {
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
    z-index: 8;
    padding-top: 20px; }
  .header__content {
    display: flex;
    justify-content: space-between; }
  .header__btn b, .header__btn a, .header__btn b i {
    color: #fff;
    &:hover {
      color: #ff0057; } }
  .header__btn b:hover i {
    color: #ff0057; }
  .header__btn a i {
    background: #fff;
    color: #000; }
  .whatsapp__intro {
    background-image: url('../img/landing-home/login-bg.png');
    padding: 130px 0;
    @include cover;
    @include r(767) {
      padding: 100px 0 50px; }
    &__title {
      max-width: 1000px;
      @include relative-x;
      h1 {
        text-align: center;
        @include r(767) {
          font-size: 31px; } }
      strong {
        font-size: 25px;
        font-weight: 600;
        line-height: 1.2;
        text-align: center;
        color: #ffffff;
        display: inline-block;
        margin-bottom: 25px;
        max-width: 900px;
        width: 100%;
        @include relative-x;
        @include r(767) {
          font-size: 16px; } }
      p {
        text-align: center;
        max-width: 680px;
        width: 100%;
        @include relative-x;
        @include r(767) {
          font-size: 16px; } }
      a {
        @include relative-x;
        @include r(767) {
          font-size: 14px;
          padding: 4px 15px 0; }
        &.btn-terms {
          background: none;
          font-size: 14px;
          font-weight: normal;
          line-height: 1.79;
          color: rgba(255, 255, 255, 0.5);
          display: block;
          text-align: center;
          text-transform: none;
          font-style: italic;
          text-decoration: underline;
          padding-top: 15px;
          &:hover {
            color: #00fefa; } } } } }
  .whatsapp__title {
    h2 {
      width: 100%;
      @include relative-x;
      @include r(767) {
        font-size: 30px; } }
    strong {
      width: 100%;
      @include relative-x;
      font-weight: normal;
      color: #131725;
      display: block; } }



  .whatsapp__who {
    .whatsapp__title {
      padding-bottom: 75px;
      h2 {
        max-width: 580px; }
      strong {
        max-width: 450px; } }
    &__block {
      @include r(767) {
        margin-bottom: 40px;
        height: calc(100% - 40px); }
      h3 {
        margin-bottom: 15px;
        font-size: 28px; } } }

  .hiring__features__block {
    padding: 30px 34px;
    padding-bottom: 60px; }





  &__team {
    background: #dedede;
    padding-top: 75px;
    overflow: hidden;
    @include r(767) {
      padding-top: 50px; }
    .whatsapp__title {
      h2 {
        max-width: 670px; }
      strong {
        max-width: 580px; } }
    &__row {
      display: flex;
      flex-wrap: wrap; }
    &__block {
      width: 100%;
      max-width: 20%;
      flex: 0 0 20%;
      margin-top: 60px;
      @include r(1200) {
        max-width: 33.333%;
        flex: 0 0 33.333%; }
      @include r(767) {
        max-width: 50%;
        flex: 0 0 50%; }
      span {
        font-size: 20px;
        font-weight: bold;
        line-height: 2.5;
        letter-spacing: -1px;
        text-align: center;
        color: #131725;
        margin-bottom: 15px;
        display: block;
        @include r(767) {
          margin-bottom: 0;
          font-size: 17px; } }
      img {
        height: 320px;
        @include relative-x;
        bottom: -7px;
        @include r(767) {
          height: 220px; } } } }




  &__tips {
    padding: 130px 0;
    @include r(767) {
      padding: 50px 0; }
    .whatsapp__title {
      padding-bottom: 90px;
      @include r(767) {
        padding-bottom: 50px; }
      h2 {
        max-width: 860px; }
      strong {
        max-width: 480px; } }
    &__row {
      display: flex; }
    &__block {
      width: 100%;
      height: 100%;
      @include r(992) {
        height: calc(100% - 20px);
        margin-bottom: 20px; }
      .block__img {
        height: 240px;
        @include cover; }
      .block__text {
        height: calc(100% - 240px);
        padding: 35px 35px 85px;
        position: relative;
        background: #dedede;
        @include tr;
        @include r(767) {
          padding: 20px 15px 70px; }
        p {
          font-size: 18px;
          font-weight: bold;
          color: #000;
          @include tr; }
        a {
          position: absolute;
          bottom: 40px;
          left: 35px;
          font-size: 18px;
          font-weight: bold;
          color: #000;
          @include r(767) {
            bottom: 20px;
            left: 15px; } }
        &:hover {
          background: #000;
          a, p {
            color: #fff; } } } } }



  .whatsapp__baner {
    h2 {
      max-width: 860px; }
    a {
      @include r(400) {
        font-size: 12px; } } } }
