.hiring {
  .whatsapp__intro {
    &__title {
      max-width: 100%;
      p {
        max-width: 510px;
        width: 100%; }
      a {
        text-transform: uppercase; } }
    &__img {
      height: 100%;
      @include center;
      img {
        max-width: 100%; } }
    h2 {
      margin-bottom: 60px; }
    &__row {
      align-items: stretch;
      justify-content: center; }
    &__block {
      margin: 0 25px;
      max-width: 450px;
      width: 100%;
      @include r(767) {
        margin: 0 0 50px; }

      div {
        min-height: 370px;
        overflow: hidden;
        border-radius: 15px;
        @include cover;
        padding: 50px 30px; }
      h4 {
        font-size: 25px;
        font-weight: bold;
        text-align: center;
        margin-bottom: 15px; }
      p {
        font-size: 16px;
        font-weight: normal;
        text-align: center;
        color: rgba(255, 255, 255, 0.7);
        max-width: 290px;
        width: 100%;
        @include relative-x;
        margin-bottom: 35px;
        display: block; }
      ul {
        display: flex;
        flex-direction: column;
        align-items: flex-end; }
      li {
        font-size: 14px;
        font-weight: bold;
        color: #131725;
        width: 200px;
        margin-bottom: 15px;
        display: flex;
        i {
          font-size: 14px;
          margin-right: 15px; } } } }
  .whatsapp__who {
    h3 {
      font-size: 24px;
      margin-bottom: 22px; }
    .whatsapp__title {
      padding-bottom: 100px; } }


  &__system {
    padding: 0 0 200px;
    @include r(767) {
      padding: 50px 0; }
    .whatsapp__title {
      margin-bottom: 85px;
      @include r(767) {
        margin-bottom: 50px; } }
    &__row {
      display: flex;
      @include r(992) {
        flex-wrap: wrap; } }
    &__block {
      border-radius: 10px;
      background-color: #f0f0f1;
      margin: 0 15px;
      @include center-column;
      justify-content: flex-start;
      padding: 50px 25px;
      width: 100%;
      @include r(992) {
        width: 45%;
        margin-bottom: 30px; }
      @include r(767) {
        width: 100%;
        margin: 0 0 30px; }
      img {
        height: 50px;
        margin-bottom: 25px; }
      h3 {
        font-size: 25px;
        font-weight: bold;
        text-align: center;
        color: #131725;
        min-height: 50px;
        line-height: 1;
        margin-bottom: 25px; }
      p {
        font-size: 16px;
        line-height: 1.56;
        text-align: center;
        color: rgba(19, 23, 37, 0.8); } } }




  &__features {
    padding: 100px 0;
    @include r(767) {
      padding: 50px 0; }
    .whatsapp__title {
      padding-bottom: 90px;
      @include r(767) {
        padding-bottom: 40px; } }
    &__row {
      display: flex;
      flex-wrap: wrap; }
    &__block {
      max-width: 25%;
      flex: 0 0 25%;
      width: 100%;
      padding: 30px 40px;
      border-right: solid 1px rgba(#000, 0.2);
      border-top: solid 1px rgba(#000, 0.2);
      position: relative;
      @include r(1200) {
        padding: 25px 15px; }
      &:nth-child(4n) {
        border-right: none; }
      &:nth-child(-n+4) {
        border-top: none; }
      @include r(992) {
        max-width: 50%;
        flex: 0 0 50%;
        border-right: solid 1px rgba(#000, 0.2);
        border-top: solid 1px rgba(#000, 0.2) !important;
        &:nth-child(2n) {
          border-right: none; }
        &:nth-child(-n+2) {
          border-top: none !important; }
        &.empty {
          display: none; }
        &.empty-992 {
          display: block; } }

      @include r(460) {
        max-width: 100%;
        flex: 0 0 100%;
        border: none !important;
        border-bottom: solid 1px rgba(#000, 0.2) !important;
        &.empty-992 {
          display: none; } }
      img {
        height: 50px;
        margin-bottom: 15px;
        @include relative-x; }
      h3 {
        font-size: 18px;
        font-weight: 600;
        text-align: center;
        color: #131725;
        margin-bottom: 15px; }
      p {
        font-size: 14px;
        font-weight: normal;
        text-align: center;
        color: #131725; }
      &__btn {
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: 0 15px;
        position: absolute;
        bottom: 25px;
        left: 0;
        width: 100%;
        a {
          font-size: 14px;
          font-weight: normal;
          text-align: center;
          color: #131725;
          &:hover {
            i {
              transform: scale(1.3); } }
          i.icon-play {
            font-size: 12px;
            margin-right: 10px;
            color: #00fefa;
            background: #000;
            border-radius: 100%;
            @include center;
            display: inline-flex;
            @include tr;
            width: 10px;
            height: 10px; }
          i.icon-arrow-right {
            font-weight: 100;
            font-size: 10px;
            @include tr;
            margin-left: 10px; }
          &:last-child:hover {
            text-decoration: underline; } }
        a.btn {
          border-radius: 19px;
          border: solid 1px #131725;
          background-color: #131725;
          padding: 10px 30px;
          font-size: 14px;
          font-weight: 600;
          text-align: center;
          color: #ffffff;
          position: relative;
          top: 20px;
          &:hover {
            background: none;
            color: #131725;
            text-decoration: none; } } } } } }
