.home {
  h2, h1 {
    font-size: 80px;
    font-weight: bold;
    line-height: 0.94;
    letter-spacing: -4px;
    text-align: center;
    color: #131c2e;
    position: relative;
    left: 50%;
    transform: translate(-50%);
    width: 100%;
    @include r(767) {
      font-size: 34px;
      letter-spacing: normal; } }
  h1 {
    line-height: 1.06;
    max-width: 875px;
    width: 100%; }




  .intro {
    background-image: url('../img/home/home-header.png');
    background-position: 100% 0;
    background-size: auto 90%;
    padding-top: 200px;
    padding-bottom: 20px;
    @include r(767) {
      min-height: 100%;
      padding: 120px 0 50px; }
    @include r(550) {
      background-size: auto 70%; }
    @include r(450) {
      background-size: 85% auto; }
    h1 {
      margin-bottom: 25px; }
    p {
      color: #616161;
      max-width: 780px;
      margin-bottom: 90px;
      @include r(767) {
        margin-bottom: 40px; } }
    a {
      background: #131c2e;
      @include tr;
      @include center;
      width: 265px;
      height: 60px;
      border-radius: 30px;
      font-size: 18px;
      border: none;
      display: flex !important;
      font-weight: bold;
      line-height: 1.39;
      color: #ffffff;
      &:hover {
        background: rgba(#131c2e, 0.8);
        i {
          transform: translate(8px); } }
      i {
        font-size: 14px;
        color: #006dff;
        margin-left: 13px;
        @include tr; } }
    &__row {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
      margin-top: 80px;
      @include r(992) {
        justify-content: center; }
      img {
        max-height: 65px;
        max-width: 125px;
        margin: 0 10px;
        @include r(1200) {
          max-width: 100px; }
        @include r(992) {
          margin: 10px 15px; } } } }




  &__bots {
    background: rgb(230, 230, 230);
    padding: 125px 0 0;
    @include r(767) {
      padding-top: 60px; }
    h3 {
      font-size: 40px;
      font-weight: bold;
      letter-spacing: -0.8px;
      text-align: center;
      color: #131c2e;
      padding: 0 40px;
      margin-bottom: 75px;
      @include r(992) {
        padding: 0; }
      @include r(767) {
        font-size: 24px; } }
    a {
      background: #131c2e;
      width: 100%;
      @include center;
      padding: 20px;
      font-size: 25px;
      font-weight: bold;
      color: #ffffff;
      margin-top: 110px;
      @include r(767) {
        font-size: 18px; }
      i {
        margin-left: 20px;
        color: #006dff;
        font-size: 14px;
        font-weight: 900;
        @include tr; }
      &:hover {
        i {
          margin-left: 30px; } } }

    &__row {
      display: flex;
      justify-content: space-around;
      @include r(767) {
        flex-direction: column;
        align-items: center; } }
    &__block {
      max-width: 430px;
      @include center-column;
      margin: 0 15px;
      @include r(767) {
        margin-bottom: 50px; }
      img {
        width: 130px;
        margin-bottom: 20px; }
      strong {
        font-size: 50px;
        font-weight: bold;
        line-height: 1;
        letter-spacing: -1px;
        text-align: center;
        color: #131c2e;
        max-width: 300px;
        margin-bottom: 30px;
        display: inline-block;
        @include r(767) {
          font-size: 30px; } }
      p {
        font-size: 18px;
        font-weight: 600;
        line-height: 1.39;
        text-align: center;
        color: #131c2e;
        @include r(767) {
          font-size: 16px; } } } }




  &__bots-whatsapp {
    padding: 110px 0;
    @include r(767) {
      padding: 50px 0; }
    &__title {
      padding-bottom: 100px;
      @include r(767) {
        padding-bottom: 40px; }
      h2 {
        margin-bottom: 25px; }
      p {
        font-size: 25px;
        font-weight: normal;
        line-height: 1.4;
        text-align: center;
        color: #616161;
        max-width: 820px;
        width: 100%;
        @include relative-x;
        display: block;
        @include r(767) {
          font-size: 18px; } } }
    .swiper-bots-next, .swiper-bots-prev {
      background: none;
      font-size: 30px;
      opacity: 1;
      @include r(767) {
        font-size: 24px; }
      &.swiper-button-disabled {
        i {
          color: #131725;
          @include center;
          @include r(767) {
            width: 22px;
            height: 22px; } } } }
    .swiper-bots-next {
      right: 5px;
      @include r(767) {
        right: 0px; } }
    .swiper-bots-prev {
      left: 2px; }
    .swiper-bots {
      padding: 0 40px;
      position: relative;
      @include r(767) {
        padding: 0 30px; }
      &:before, &::after {
        content: '';
        width: 35px;
        height: 100%;
        background: #fff;
        position: absolute;
        top: 0;
        z-index: 6;
        @include r(767) {
          width: 25px; } }
      &::after {
        left: -1px; }
      &::before {
        right: -1px; }
      &__img {
        height: 35px;
        @include center;
        margin-bottom: 15px;
        img {
          max-width: 35px;
          max-height: 35px; } }
      &__slide {
        padding: 35px;
        padding-right: 20px;
        border-radius: 25px;
        background-color: rgba(#a9a9a9, 0.2);
        @include center-column;
        justify-content: flex-start;
        min-height: 340px;
        @include r(767) {
          padding: 20px;
          min-height: 100%; } }

      &__info {
        min-height: 130px;
        padding-bottom: 25px;
        position: relative;
        @include r(767) {
          min-height: auto; }
        &::before {
          content: '';
          background-color: #131c2e;
          height: 1px;
          width: 45px;
          display: block;
          position: absolute;
          bottom: 0;
          left: 0; }
        b {
          font-size: 18px;
          font-weight: 600;
          display: block;
          text-align: center;
          color: #131725;
          margin-bottom: 20px; }
        p {
          font-size: 14px;
          font-weight: normal;
          line-height: 1.25;
          color: #131725; } }
      strong {
        margin-top: 15px;
        display: block;
        font-size: 12px;
        font-weight: 600;
        color: #131725;
        min-height: 30px; }
      &__btn {
        margin-top: 25px;
        @include center;
        a {
          margin: 0 6px;
          &:hover {
            i {
              transform: scale(1.2); } } }
        i {
          font-size: 20px;
          @include center;
          @include tr;
          &.icon-arrow_left-cicle {
            color: #131725; }
          &.icon-play {
            color: #28e6f1;
            background: #131725;
            border-radius: 50%;
            width: 18px;
            height: 18px; } } } } }




  &__bots-info {
    @include r(767) {
      padding-top: 100px; }
    h2 {
      margin-bottom: 170px;
      max-width: 880px;
      @include r(767) {
        margin-bottom: 40px; } }
    &__row {
      display: flex;
      align-items: flex-start;
      margin-bottom: 160px;
      @include r(767) {
        margin-bottom: 80px; }
      &--left {
        @include r(992) {
          flex-direction: column; }
        .home__bots-info__img {
          justify-content: flex-end;
          padding-left: 20px; } }
      &--right {
        @include r(992) {
          flex-direction: column-reverse; }
        .home__bots-info__img {
          padding-right: 20px; } } }
    &__text {
      max-width: 525px;
      width: 100%;
      h3 {
        font-size: 50px;
        font-weight: bold;
        line-height: 1.1;
        letter-spacing: -1px;
        color: #131c2e;
        margin-bottom: 45px;
        @include r(767) {
          font-size: 27px;
          text-align: center;
          margin-bottom: 25px; } }
      p {
        font-size: 18px;
        font-weight: normal;
        line-height: 1.53;
        color: #131c2e;
        @include r(767) {
          text-align: center;
          font-size: 16px; } } }
    &__img {
      flex: 1;
      display: flex;
      @include r(992) {
        padding: 0 !important;
        margin-top: 40px; }
      div {
        width: 200px;
        border-radius: 25px;
        box-shadow: 0px 3px 107.5px 0 rgba(0, 0, 0, 0.23);
        background-color: #ffffff;
        overflow: hidden;
        border: 5px solid #fff;
        margin: 0 7px;
        @include center;
        @include r(1200) {
          width: 160px; }
        @include r(992) {
          width: 100%;
          border-radius: 15px; }
        img {
          width: 100%; } } } }






  &__platform {
    padding: 100px 0 140px;
    @include r(767) {
      padding: 50px 0 0; }
    &__title {
      h2 {
        max-width: 1140px;
        margin-bottom: 150px;
        @include r(767) {
          margin-bottom: 30px; } }
      p {
        font-size: 25px;
        line-height: 1.4;
        text-align: center;
        color: #171717;
        max-width: 810px;
        @include relative-x;
        display: block;
        padding-bottom: 140px;
        @include r(767) {
          padding-bottom: 90px;
          font-size: 18px; } } }
    .message__channels-other__block {
      position: relative;
      background-color: #2cfffe;
      min-height: 345px;
      justify-content: flex-start;
      margin: 0;
      margin-bottom: 30px;
      padding: 25px 30px;
      @include r(767) {
        margin-bottom: 50px; }
      &--primary {
        background: rgb(113, 242, 186);
        img {
          width: 100%;
          max-width: 180px;
          position: absolute;
          bottom: -35px;
          right: 20px;
          @include r(1200) {
            bottom: -80px;
            right: -10px; }
          @include r(992) {
            bottom: -30px; }
          @include r(767) {
            right: -10px; }
          @include r(400) {
            max-width: 130px; } } }
      &--secondary {
        background: rgb(114, 238, 250);
        img {
          width: 100%;
          max-width: 370px;
          position: absolute;
          bottom: -30px;
          right: -10px;
          @include r(1200) {
            bottom: -35px; }
          @include r(767) {
            right: -10px; } } }
      &--dx {
        background: rgb(175, 137, 242);
        img {
          width: 100%;
          max-width: 90px;
          position: absolute;
          bottom: -20px;
          right: 0px;
          @include r(1200) {
            right: -10px; } } }



      a {
        font-size: 18px;
        font-weight: 600;
        color: #131c2e;
        i {
          font-size: 12px;
          color: #006dff; } } } }




  &__businesses {
    padding-top: 150px;
    @include r(767) {
      padding-top: 50px; }
    &__title {
      padding: 0px 0 95px;
      @include r(767) {
        padding: 0px 0 50px; }
      h2 {
        font-size: 50px;
        margin-bottom: 35px;
        max-width: 100%;
        line-height: 1.1;
        letter-spacing: -1px;
        @include r(767) {
          font-size: 24px; } }
      p {
        font-size: 25px;
        font-weight: normal;
        line-height: 1.4;
        text-align: center;
        color: #9e9e9e;
        max-width: 705px;
        width: 100%;
        @include relative-x;
        @include r(767) {
          font-size: 18px; } } }
    &__current-ideas {
      padding-bottom: 200px;
      @include r(767) {
        padding-bottom: 50px; }
      &__block {
        @include cover;
        background-size: contain;
        @include center-column;
        padding: 60px 0px;
        height: 100%;
        min-height: 385px;
        @include tr;
        &:hover {
          transform: scale(1.06); }
        &--1 {
          background-image: url('../img/home/shape-1.png'); }
        &--2 {
          background-image: url('../img/home/shape-2.png'); }
        &--3 {
          background-image: url('../img/home/shape-3.png'); }
        @include r(767) {
          margin-bottom: 40px;
          height: auto; }
        h4 {
          font-size: 25px;
          font-weight: bold;
          text-align: center;
          color: #ffffff;
          max-width: 240px;
          width: 100%;
          display: block;
          margin-bottom: 25px; }
        a {
          font-size: 18px;
          font-weight: bold;
          line-height: 1.39;
          color: #ffffff;
          &:hover {
            text-decoration: underline;
            i {
              transform: translate(8px); } }
          i {
            margin-left: 10px;
            font-size: 12px;
            color: #006dff;
            @include tr; } } } } }





