  &__firms {
    padding-bottom: 190px;
    @include r(767) {
      padding-bottom: 50px; }
    h2 {
      max-width: 100%;
      margin-bottom: 75px;
      @include r(767) {
        margin-bottom: 40px; } }
    &__row {
      display: flex;
      flex-wrap: wrap;
      justify-content: center; }

    &__block {
      margin: 0 15px;
      margin-bottom: 20px;
      @include center;
      img {
        max-width: 100%; }
      &:nth-child(1) {
        img {
          height: 22px; } }
      &:nth-child(2) {
        img {
          height: 34px; } }
      &:nth-child(3) {
        img {
          height: 23px; } }
      &:nth-child(4) {
        img {
          height: 46px; } }
      &:nth-child(5) {
        img {
          height: 16px; } } } } }




@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .home__presentation h2, .home__presentation li {
    color: #cca56e;
    background: none; } }
