.blocker {
  z-index: 9999 !important; }
.pop-up {
  .close-modal {
    display: none !important; }
  &__close {
    width: 30px;
    height: 30px;
    position: absolute;
    right: 20px;
    top: 20px;
    span {
      &:before, &:after {
        content: '';
        width: 15px;
        height: 2px;
        background: gray;
        position: absolute;
        top: 15px;
        right: 8px;
        @include tr; }
      &:before {
        transform: rotate(45deg); }
      &:after {
        transform: rotate(-45deg); } }
    &:hover {
      span:before, span:after {
        background: red; } } }
  &__pay.modal {
    max-width: 1220px;
    width: 100%;
    border-radius: 10px;
    box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.19);
    background-color: $white;
    padding: 0;
    overflow: hidden;
    .pricing__advantages {
      margin-top: 0; }
    .pop-up__close {
      z-index: 2;
      top: 10px;
      background: #fff; } }
  &__schedule {
    padding: 0 !important;
    width: 80%;
    min-width: 901px;
    max-width: 1000px;
    height: 90%;
    max-height: 680px;
    @include r(1040) {
      min-width: auto;
      max-width: 100%;
      width: 95%; }
    iframe {
      width: 100%;
      height: 100%; }
    .pop-up__close {
      right: -35px;
      top: -10px; } }
  &__video {
    padding: 0 !important;
    width: auto !important;
    min-width: auto;
    max-width: 1000px;
    height: 90%;
    max-height: 600px;
    background: none !important;
    box-shadow: none !important;
    @include r(1040) {
      max-width: 95%; }
    iframe {
      width: 100%;
      max-width: 100%;
      height: 100%; }
    .pop-up__close {
      right: -35px;
      top: -10px; }
    &#video-page {
      width: 100% !important; } } }
