.intro-dx {
  min-height: 100vh;
  position: relative;
  z-index: 9;
  padding-top: 100px;
  background-color: #0f0f20;
  background-image: url('../img/dxcard/dx-card-bg.png');
  @include cover;
  @include center-y;
  @include rh(740) {
    padding-top: 200px; }
  @include r(767) {
    padding: 200px 0 50px;
    min-height: 100vh; }
  h1, li, p, span {
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }
  &__text {
    b {
      font-size: 25px;
      font-weight: bold;
      line-height: 2.7;
      color: #cca56e;
      @include r(767) {
        font-size: 20px;
        display: block;
        text-align: center; } }
    strong {
      font-family: 'Dancing Script', cursive;
      font-size: 30px;
      font-weight: normal;
      line-height: 1;
      color: #f0d5a5;
      margin-left: 10px;
      @include r(991) {
        text-align: center;
        display: block; }
      @include r(767) {
        font-size: 20px; } }
    h1 {
      text-align: left;
      margin: 20px 0;
      padding: 5px 0;
      background-image: linear-gradient(to right bottom, #c8baa5, #c8baa5, #c8baa5, #fcefd6, #fcefd6, #a19281, #a19281, #a19281, #a19281);
      @include r(1200) {
        font-size: 40px;
        letter-spacing: normal; }
      @include r(991) {
        text-align: center; } }
    span {
      font-size: 25px;
      line-height: 1.4;
      color: #f3d9a9;
      background-image: linear-gradient(to right bottom, #cca56e, #cca56e, #f3d9a9, #f3d9a9);
      @include r(767) {
        font-size: 18px;
        text-align: center;
        display: block; } }
    p {
      font-size: 25px;
      font-weight: normal;
      line-height: 1.2;
      color: #f0d5a5;
      background-image: linear-gradient(to right bottom, #cca56e, #cca56e, #f3d9a9, #f3d9a9);
      @include r(991) {
        text-align: center; }
      @include r(767) {
        font-size: 18px; } }
    a {
      font-size: 18px;
      font-weight: bold;
      color: #e99d39;
      width: 195px;
      height: 50px;
      padding-top: 1px;
      border-radius: 25px;
      background-color: rgb(33, 31, 36);
      @include center;
      @include r(991) {
        @include relative-x; }
      &:hover {
        i {
          transform: translate(8px); } }
      i {
        color: #f2015b;
        margin-left: 10px;
        @include tr;
        font-size: 12px; } } }
  ul {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 30px;
    max-width: 520px;
    width: 100%;
    @include r(991) {
      align-items: center; } }
  li {
    font-size: 18px;
    font-weight: normal;
    line-height: 1.11;
    text-align: left;
    display: inline-block;
    padding: 20px 0 15px;
    padding-left: 50px;
    position: relative;
    background-image: linear-gradient(to right bottom, #f3d9a9, #f3d9a9, #cca56e, #cca56e);
    @include r(991) {
      max-width: 400px;
      width: 100%; }
    &:before {
      width: calc(100% - 50px);
      content: '';
      background-image: linear-gradient(to right, #4d484e, #16141a, #16141a);
      height: 1px;
      display: block;
      position: absolute;
      bottom: 0;
      right: 0; }
    img {
      width: 26px;
      margin-right: 25px;
      position: absolute;
      top: 15px;
      left: 0; } }
  h1 {
    font-size: 80px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 0.94;
    letter-spacing: -4px;
    text-align: left;
    color: #131c2e;
    margin-bottom: 25px;
    @include r(1200) {
      font-size: 50px;
      letter-spacing: normal; }
    @include r(767) {
      font-size: 30px;
      text-align: center; } }
  p {
    font-size: 25px;
    line-height: 1.4;
    color: #131c2e;
    max-width: 640px;
    width: 100%;
    @include r(767) {
      text-align: center; }
    strong {
      font-weight: bold; } } }


@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .intro-dx h1, .intro-dx li, .intro-dx p, .intro-dx span {
    color: #cca56e;
    background: none; } }



.phone {
  // width: 310px
  padding: 0 19vh;
  max-height: 81vh;
  height: 100%;
  border-radius: 40px;
  box-shadow: 0px 3px 107.5px 0 rgba(0, 0, 0, 0.23);
  position: fixed;
  transform: translate(0, -50%);
  top: 52vh;
  right: 14%;
  z-index: 99;
  transition: all 1s cubic-bezier(.43,.06,0,1.05);
  @include center-column;
  overflow: hidden;
  will-change: auto;
  border: 8px solid transparent;
  @include r(1040) {
    right: 60px; }
  @include r(767) {
    display: none; }
  &.active {
    transition: all 0.8s cubic-bezier(.43,.06,0,1.05);
    border: 8px solid #fff;
    padding: 0 18.7vh;
    background-color: #fff; }
  &.stop {
    position: absolute;
    transition: all 1s cubic-bezier(.43,.06,0,1.05);
    transform: translate(0, 0%) !important;
    border: 8px solid #fff;
    padding: 0 18.7vh;
    background-color: #fff;
    background-position: 0 -100%; }
  &__block {
    width: 100%;
    height: calc(100% + 5px);
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    top: 110%;
    transition: all 0.9s;
    @include center;
    align-items: flex-end;
    border-radius: 36px;
    overflow: hidden;
    opacity: 0;
    img {
      width: 100%; }
    &:first-child {
      height: 100%;
      align-items: center;
      padding-top: 100px;
      background: none; }
    &.active {
      top: -5px;
      z-index: 2;
      opacity: 1; }
    &.down {
      top: -110%; } } }
