.whatsapp {

  &__title {
    h2 {
      font-size: 50px;
      font-weight: bold;
      line-height: 1;
      letter-spacing: -2.5px;
      color: #131725;
      margin-bottom: 25px;
      text-align: center;
      @include r(767) {
        font-size: 38px;
        letter-spacing: -2px; } }
    p, strong {
      font-size: 18px;
      font-weight: normal;
      line-height: 1.39;
      text-align: center;
      color: #131725;
      display: block; }
    strong {
      font-weight: bold; } }
  &__intro {
    padding-top: 80px;
    background-image: url('../img/whatsapp/hotel-landing.png');
    background-repeat: no-repeat;
    background-position: 100% 0;
    background-size: contain;
    background-color: rgb(13, 12, 70);
    img {
      max-width: 390px;
      width: 100%;
      @include r(767) {
        @include relative-x;
        margin-top: 30px; } }
    p {
      font-size: 18px;
      font-weight: normal;
      line-height: 1.39;
      color: rgba(255, 255, 255, 0.6);
      margin-bottom: 65px;
      text-align: center;
      @include r(767) {
        text-align: center;
        margin-bottom: 30px; } }
    &__title {
      padding-top: 70px;
      max-width: 560px;
      width: 100%;
      h1 {
        font-size: 65px;
        font-weight: bold;
        line-height: 1.08;
        letter-spacing: -3.25px;
        color: #ffffff;
        margin-bottom: 20px;
        @include r(767) {
          text-align: center;
          font-size: 45px;
          letter-spacing: -1px; } }
      p {
        text-align: left; }
      a {
        padding: 4px 45px 0;
        font-size: 18px;
        line-height: 3.2;
        font-weight: bold;
        color: #131725;
        text-transform: uppercase;
        border-radius: 29px;
        background-color: #00fefa;
        display: inline-flex;
        align-items: center;
        &:hover {
          i {
            left: 10px;
            transform: scale(1.4); } }
        @include r(767) {
          @include relative-x;
          padding: 4px 30px 0; }
        i {
          color: #131725;
          margin-left: 10px;
          margin-top: -4px;
          font-weight: 900;
          @include tr;
          position: relative;
          left: 0; } } }
    h2 {
      margin-top: 130px;
      font-size: 50px;
      font-weight: bold;
      line-height: 1;
      letter-spacing: -2.5px;
      text-align: center;
      color: #ffffff;
      max-width: 1000px;
      width: 100%;
      @include relative-x;
      margin-bottom: 10px;
      @include r(767) {
        margin-bottom: 40px;
        margin-top: 50px;
        font-size: 35px;
        letter-spacing: -2px; } }
    &__row {
      display: flex;
      align-items: center;
      justify-content: space-around;
      padding-bottom: 45px;
      @include r(767) {
        flex-direction: column;
        align-items: center; }
      img {
        width: 40%;
        @include r(767) {
          width: 100%;
          transform: none;
          position: static; } }
      .old-way {
        max-width: 451px; }
      .vs {
        max-width: 60px;
        margin: 0 50px;
        @include r(767) {
          margin: 20px 0; } }
      .newway {
        max-width: 445px; } } }





  &__who {
    padding-top: 80px;
    padding-bottom: 100px;
    @include r(992) {
      padding-bottom: 0; }
    .whatsapp__title {
      padding-bottom: 160px; }
    &__block {
      border-radius: 10px;
      background-color: #131725;
      position: relative;
      padding: 45px 30px;
      height: 100%;
      @include r(1200) {
        padding: 45px 20px; }
      @include r(992) {
        margin-bottom: 140px;
        height: calc(100% - 140px); }
      .line {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 12px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px; }
      h3 {
        background-image: linear-gradient(to right, #43ea80, #43ea80, #38f8d4, #38f8d4);
        font-size: 30px;
        font-weight: bold;
        text-align: left;
        line-height: 1;
        color: #f0f0f0;
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent; }
      a {
        font-size: 14px;
        font-weight: 600;
        color: #2af1fd;
        padding: 25px 0;
        display: inline-flex;
        @include tr;
        &:hover {
          transform: translate(5px);
          i {
            transform: scale(1.3); } }
        i {
          padding-left: 10px;
          font-size: 13px;
          margin-top: 2px;
          @include tr; } }
      img {
        position: absolute;
        height: 130px;
        top: -130px;
        left: 50%;
        transform: translate(-50%); }
      p {
        font-size: 16px;
        font-weight: normal;
        line-height: 1.56;
        color: rgba(255, 255, 255, 0.7); } } }



  &__benefits {
    background: rgb(240, 240, 240);
    padding: 100px 0;
    @include r(767) {
      padding: 50px 0 0; }
    .whatsapp__title {
      padding-bottom: 80px; }
    &__block {
      display: flex;
      flex-direction: column;
      align-items: center;
      @include r(992) {
        margin-bottom: 50px; }
      &--secondary {
        padding-top: 70px;
        @include r(992) {
          padding-top: 0; } }
      img {
        height: 70px;
        margin-bottom: 15px; }
      h3 {
        font-size: 18px;
        font-weight: 600;
        text-align: center;
        color: #131725;
        width: 100%;
        max-width: 210px;
        margin-bottom: 20px; }
      p {
        font-size: 14px;
        font-weight: normal;
        text-align: center;
        color: rgba(19, 23, 37, 0.8);
        max-width: 230px;
        width: 100%; } } }





  &__features {
    padding: 100px 0;
    @include r(767) {
      padding: 50px 0; }
    .whatsapp__title {
      padding-bottom: 90px;
      @include r(767) {
        padding-bottom: 40px; } }
    &__section {
      strong {
        font-size: 14px;
        font-weight: bold;
        color: #ff0057;
        text-transform: uppercase;
        margin-bottom: 30px;
        display: block; } }
    &__row {
      display: flex;
      flex-wrap: wrap; }
    &__block {
      flex: 0 0 25%;
      max-width: 25%;
      padding-right: 10px;
      padding-left: 60px;
      margin-bottom: 60px;
      border-right: 1px solid rgba(#41414f, 0.3);
      &:nth-child(4n) {
        border: none;
        @include r(992) {
          border-right: 1px solid rgba(#41414f, 0.3); } }
      &:last-child {
        border-right: none; }
      &:nth-child(2n) {
        @include r(992) {
          border-right: none; } }
      @include r(992) {
        flex: 0 0 50%;
        max-width: 50%; }
      @include r(575) {
        flex: 0 0 100%;
        max-width: 100%;
        border-right: none; }
      h4 {
        font-size: 18px;
        font-weight: 600;
        color: #131725;
        margin-bottom: 15px;
        position: relative;
        img {
          width: 24px;
          position: absolute;
          left: -40px;
          top: 0px;
          &.small {
            width: 18px; } } }
      p {
        font-size: 14px;
        font-weight: normal;
        line-height: 1.25;
        color: #131725;
        padding-bottom: 30px;
        display: block;
        position: relative;
        &::before {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 45px;
          height: 1px;
          background: #131c2e; } }
      b {
        font-size: 12px;
        font-weight: 600;
        color: #131725;
        margin-top: 15px;
        margin-bottom: 20px;
        display: block;
        min-height: 16px; }
      &__btn {
        display: flex;
        a:first-child {
          margin-right: 10px;
          i {
            font-size: 15px;
            color: #00fefa;
            width: 13px;
            height: 13px;
            border-radius: 50%;
            @include center;
            background: #000; } }
        a:last-child {
          i {
            font-size: 15px;
            color: #000;
            width: 13px;
            height: 13px;
            @include center; } }
        a:hover {
          transform: scale(1.2); } } } }




  &__pricing {
    padding: 100px 0;
    background: rgba(#000, 0.1);
    @include r(767) {
      padding: 50px 0; }
    &__select {
      strong {
        font-size: 14px;
        font-weight: bold;
        color: #131c2e;
        margin-bottom: 10px;
        display: block;
        text-align: center; }
      ul {
        display: flex;
        justify-content: center;
        padding-left: 5px; }
      li {
        border-radius: 17px;
        padding: 8px 15px 6px;
        margin-right: 5px;
        font-size: 14px;
        font-weight: normal;
        color: #131c2e;
        border: 1px solid transparent;
        @include tr;
        @include r(767) {
          padding: 6px 8px 5px; }
        &:hover {
          border: 1px solid #fff; }
        &.active {
          background-color: #ffffff;
          font-weight: 600; } } }
    &__block {
      margin-top: 60px;
      max-width: 370px;
      width: 100%;
      @include relative-x;
      overflow: hidden;
      border-radius: 10px;
      background: #fff;
      padding-bottom: 40px;
      // strong
      //   font-size: 14px
      //   font-weight: bold
      //   text-align: center
      //   color: #131c2e
      //   background-color: #0ef7b5
      //   padding: 12px 0 10px
      //   display: block
      //   text-transform: uppercase
      h2 {
        margin-top: 50px;
        font-size: 36px;
        font-weight: bold;
        letter-spacing: -0.72px;
        text-align: center;
        color: #131c2e;
        margin-bottom: 20px; }
      h3 {
        font-size: 30px;
        font-weight: bold;
        color: #131c2e;
        text-align: center;
        margin-bottom: 30px;
        span {
          position: relative;
          top: -10px;
          margin-right: 5px;
          font-size: 14px;
          font-weight: normal; }
        b {
          font-size: 14px; } }
      ul {
        width: 220px;
        @include relative-x;
        li {
          font-size: 16px;
          font-weight: normal;
          color: #131c2e;
          margin-bottom: 7px;
          display: flex;
          align-items: center;
          i.icon-check {
            color: #0ef7b5;
            margin-right: 10px; } } }
      a.btn {
        font-size: 16px;
        font-weight: bold;
        color: #ffffff;
        padding: 13px 69px;
        border-radius: 25px;
        background-color: #131c2e;
        border: 1px solid #131c2e;
        display: inline-flex;
        @include relative-x;
        margin-top: 30px;
        @include tr;
        &:hover {
          background: none;
          color: #131c2e; } } } }





  &__baner {
    padding: 80px 0;
    background: rgb(13, 12, 60);
    @include r(767) {
      padding: 50px 0; }
    .whatsapp__title {
      padding-bottom: 40px; }
    h2 {
      color: #fff;
      margin-bottom: 10px; }
    p {
      color: #fff; }
    a {
      padding: 4px 45px 0;
      font-size: 18px;
      line-height: 3.2;
      font-weight: bold;
      color: #131725;
      text-transform: uppercase;
      border-radius: 29px;
      background-color: #00fefa;
      display: inline-flex;
      align-items: center;
      @include relative-x;
      &:hover {
        i {
          left: 10px;
          transform: scale(1.4); } }
      @include r(767) {
        @include relative-x;
        padding: 4px 20px 0;
        font-size: 14px; }
      i {
        color: #131725;
        margin-left: 10px;
        margin-top: -4px;
        font-weight: 900;
        @include tr;
        position: relative;
        left: 0; } } }


  &__footer {
    padding: 50px 0;
    background: rgb(245, 245, 245);
    &__block {
      @include r(1200) {
        width: 50%; }
      @include r(767) {
        width: 100%; } }
    &__row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap; }
    .footer__logo {
      padding: 0;
      @include r(767) {
        margin-bottom: 20px; } }
    .footer__copyright, .footer__policy {
      margin: 0;
      @include r(1200) {
        margin-top: 40px; }
      @include r(767) {
        margin-top: 20px; } } } }
