.footer {
  background-color: #f5f5f5;
  padding: 105px 0 40px;
  &-dx {
    @include r(767) {
      display: none; }
    @include rh(600) {
      display: none; } }
  @include r(767) {
    padding: 20px 0 30px; }
  &__logo {
    @include center-y;
    padding: 10px 0 55px;
    position: relative;
    @include r(1040) {
      padding-bottom: 30px; }
    @include r(767) {
      justify-content: center; }
    img {
      height: 50px;
      width: 50px;
      margin-right: 20px;
      @include r(767) {
        margin-right: 15px; } }
    p {
      font-size: 14px;
      line-height: 1.43;
      font-weight: bold;
      color: #131c2e;
      span {
        font-size: 12px;
        font-weight: normal;
        display: block; } } }
  &__social {
    @include r(767) {
      display: flex;
      justify-content: center;
      margin-bottom: 30px; }
    a {
      margin-right: 20px;
      font-size: 24px;
      color: rgba(#131725, 0.3);
      &:hover {
        color: #ff0057; }
      @include r(767) {
        margin-right: 0;
        padding: 0 10px; } } }
  &__contacts {
    padding: 25px 0 30px;
    p {
      font-size: 14px;
      line-height: 1.43; } }
  &__nav {
    display: flex;
    justify-content: flex-end;
    @include r(767) {
      @include center-column; }
    &__block {
      margin-left: 75px;
      @include r(992) {
        margin-left: 25px; }
      @include r(767) {
        width: 100%; }
      h3 {
        margin-bottom: 30px;
        font-size: 12px;
        font-weight: bold;
        color: #131725;
        text-transform: uppercase;
        @include r(767) {
          margin-bottom: 20px;
          display: block;
          width: calc(100% + 30px);
          border-bottom: 1px solid #e6e6e6;
          position: relative;
          margin-bottom: 0;
          left: 50%;
          padding: 16px;
          transform: translate(-50%);
          color: #4d4d4d;
          font-weight: 700; }
        i {
          display: none;
          position: absolute;
          right: 20px;
          top: 50%;
          transform: translate(0, -50%);
          font-weight: 300;
          color: #999;
          @include tr;
          @include r(767) {
            display: block; } }
        &.active {
          i {
            transform: translate(0, -50%) rotate(180deg);
            color: #666; } } }
      &:first-child {
        h3 {
          @include r(767) {
            border-top: 1px solid #e6e6e6; } } }
      ul {
        @include r(767) {
          display: none;
          width: calc(100% + 30px);
          border-bottom: 1px solid #e6e6e6;
          left: 50%;
          position: relative;
          padding: 20px 10px 10px;
          padding-left: 30px;
          transform: translate(-50%); } }
      li {
        margin-bottom: 15px;
        a {
          font-size: 14px;
          color: #131c2e;
          font-weight: 400;
          &:hover {
            text-decoration: underline; } } } } }
  &__copyright {
    margin-top: 40px;
    @include r(767) {
      margin-top: 20px;
      text-align: center; }
    p {
      font-size: 12px;
      color: #5f626b;
      line-height: 1.4; } }
  &__policy {
    margin-top: 40px;
    display: flex;
    justify-content: flex-end;
    @include r(767) {
      margin-top: 20px;
      justify-content: center; }
    a, span {
      font-size: 12px;
      color: #5f626b;
      @include tr; }
    span {
      margin: 0 5px; }
    a:hover {
      text-decoration: underline; } } }
